@import url('https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,400;0,700;1,100;1,200;1,400;1,700&family=Open+Sans:ital,wght@0,300;0,400;1,300;1,400&display=swap')

$main-font: 'Open Sans', sans-serif
$second-font: 'Kanit', sans-serif

$green: #558E50

input,textarea,button,select,a, .arrow
  -webkit-tap-highlight-color: transparent

body
  position: relative
  letter-spacing: 0.5px
  font-family: $second-font

ul,li
  list-style-type: none

a
  color: white
  text-decoration: none

.background
  position: fixed
  top: 0
  width: 100%
  height: 100%
  background: linear-gradient(0deg, rgb(50 50 50 / 50%) 100%, rgba(0, 0, 0, 0.8) 100%),url('../public/images/background.jpg') 50%/cover no-repeat, rgba(255, 255, 255, 0.80)

.main-container
  position: relative
  display: grid
  grid-template-columns: 250px 1fr
  max-width: 1440px
  background: rgba(40, 40, 40, 0.80)
  backdrop-filter: blur(10px)
  margin: 0 auto

  @media screen and ( max-width: 1100px )
    display: flex
    flex-direction: column

  .leftbar
    position: sticky
    z-index: 200
    top: 0
    height: 100vh
    background: rgba(39, 39, 39, 0.50)
    box-shadow: 2px 0px 20px 2px rgba(0, 0, 0, 0.30)

    @media screen and ( max-width: 1100px )
      display: flex
      background: rgba(39, 39, 39, 1)
      height: 100px

    .language
      text-align: center
      color: white

    .menu
      padding: 40px 20px
      display: flex
      position: relative
      justify-content: center
      flex-direction: column
      text-transform: uppercase
      font-style: italic
      @media screen and ( max-width: 1100px )
        display: flex
        width: 100%
        flex-direction: row
        justify-content: space-between
        padding: 10px 20px
        gap: 40px

      .logo
        display: flex
        justify-content: center
        align-items: center

      .menu-button
        display: flex
        align-items: center
        transition: all .4s
        cursor: pointer

        svg
          transition: all .4s

          #rect1, #rect2, #rect3
            transition: all .4s

        &.active
          #rect1
            rotate: 30deg
          #rect2
            opacity: 0
            width: 0
          #rect3
            rotate: -30deg
            transform-origin: 0 23px

        @media screen and ( min-width: 1100px )
          display: none

        a
          display: flex

        @media screen and ( max-width: 1100px )
          align-items: center
      .navbar
        display: flex
        flex-direction: column
        align-items: center
        margin-top: 60px
        @media screen and ( max-width: 1100px )
          margin-top: 0
          display: none
          justify-content: center
        ul, li
          margin: 0
          padding: 0
        ul
          display: flex
          flex-direction: column
          gap: 15px
          @media screen and ( max-width: 1100px )
            flex-direction: row
            

      .navbar-mobile
        display: flex
        position: absolute
        padding: 40px 0
        flex-direction: column
        align-items: center
        justify-content: center
        background: rgba(39, 39, 39, 1)
        top: -110px
        left: 0
        opacity: 0
        width: 100%
        transition: all .4s
        &.active
          top: 110px
          opacity: 1
        ul, li
          margin: 0
          padding: 0
        ul
          display: flex
          flex-direction: column
          gap: 20px
          justify-content: center
          align-items: center

  .rightbar
    width: 100%
    min-height: 100vh

    .content
      display: flex
      padding-top: 40px
      flex-direction: column
      align-items: flex-start
      gap: 25px
      flex: 1 0 0
      align-self: stretch

      .content__header
        display: flex
        padding: 0 40px
        font-size: 16px
        gap: 20px
        color: white
        font-style: italic
        align-items: center
        text-transform: uppercase
        @media screen and ( max-width: 720px )
          padding: 0 20px

        .cup-icon
          object-fit: contain
          height: 30px

        .discipline-icon
          object-fit: contain
          height: 30px

        .content__header__title
          display: flex
          flex-direction: column
          gap: 7px

      .match
        display: flex
        padding: 0 40px
        align-items: center
        justify-content: space-between
        width: calc( 100% - 80px )
        height: auto
        color: white
        text-transform: uppercase
        font-style: italic
        @media screen and ( max-width: 720px )
          padding: 0 20px
          width: calc(100% - 40px)
        @media screen and ( max-width: 430px )
          display: grid
          grid-template-rows: 1fr max-content
          justify-content: space-evenly

        .go-bet-link
          padding: 10px 10px 10px 10px
          border-radius: 10px
          gap: 10px
          border-radius: 10px
          text-align: center
          color: #272727
          font-size: 14px
          transition: all .1s
          margin-bottom: 50px
          font-weight: bold
          background: #EED067
          &:hover
            background: #EED090
          @media screen and ( max-width: 430px )
            grid-column: 1/span 2
            margin-bottom: 0
            margin-top: 20px

        img
          width: 100px
          @media screen and ( max-width: 720px )
            width: 90px

        .team-1-wrapper, .team-2-wrapper
          display: flex
          flex-direction: column
          width: 100%
          align-items: center
          @media screen and ( max-width: 430px )
            grid-row: 1

          img
            height: 100px
            width: 100px
            object-fit: contain
            @media screen and ( max-width: 720px )
              height: 80px
              width: 80px

          .team-1-name, .team-2-name
            display: flex
            justify-content: center
            text-align: center
            height: 40px
            align-items: flex-start
            margin-top: 10px
            @media screen and ( max-width: 600px )
              font-size: 14px
            @media screen and ( max-width: 430px )
              height: max-content

      .match-info
        display: flex
        justify-content: center
        padding: 20px 0
        align-items: center
        flex-direction: column
        gap: 20px
        color: white
        font-size: 16px
        font-style: italic
        width: 100%
        background: rgba(93, 186, 216, 0.10)
        .start-time
          display: flex
          justify-content: center
          align-items: center
          gap: 20px
          font-size: 20px
          @media screen and ( max-width: 430px )
            display: grid
            grid-auto-rows: max-content
            justify-content: center
            gap: 5px
            text-align: center
        .time-left
          span
            font-size: 14px
            font-weight: bold
            color: #93BA61

      .all-bets
        display: flex
        flex-direction: column
        .bet
          display: flex
          flex-direction: column
          width: 100%
          color: white

          &.favorite-bet
            box-sizing: border-box
            border: 3px inset #FF6723
            box-shadow: 0px 0px 23px 0px rgba(255, 157, 86, 0.80) inset

          .row-1
            display: flex
            position: relative
            flex-wrap: wrap
            align-items: center
            gap: 20px
            background: rgba(0, 0, 0, 0.30)
            padding: 20px 20px

            .favorite
              display: flex
              align-items: center
              gap: 10px

            .bet__variant
              display: flex
              gap: 20px
              width: 95%
              align-items: center
              @media screen and ( max-width: 600px )
                font-size: 14px
                width: 100%
              a
                border-radius: 10px
                border: 1px solid #FFF
                padding: 5px 10px

            .arrow
              position: absolute
              right: 20px
              width: 20px
              cursor: pointer
              transition: all .3s
              transform: rotate(180deg)

              &.toggle
                transform: rotate(0deg)

              @media screen and ( max-width: 600px )
                position: relative
                align-self: center
                right: 0
                width: 100%
                height: 15px

          &__text-generation
            margin-top: 20px
            padding: 0 20px 20px 20px
            font-size: 16px
            transition: all .3s
            max-height: 100em
            overflow: hidden
            visibility: visible
            position: relative
            &.toggle
              visibility: hidden
              max-height: calc(100vw - 100vw)
            span.pre-info
              font-family: $main-font
              font-size: 16px
              position: relative
              margin-left: 25px
              display: flex
              align-items: center
              @media screen and ( max-width: 600px )
                font-size: 14px
                line-height: 145%

              &::before
                content: "!"
                display: block
                position: absolute
                font-weight: bold
                font-style: italic
                font-family: $main-font
                font-size: 40px
                color: yellow
                left: -25px

            span
              font-weight: bold

            .text
              color: #FFF
              margin-top: 10px
              font-size: 18px
              font-style: italic
              line-height: 150%
              text-align: left
              font-family: $main-font
              span
                display: block
                padding: 10px 20px 10px 0
                font-weight: bold
                color: #93BA61

.rightbar

  .h1-title
    display: block
    color: white
    font-family: $main-font
    font-size: 30px
    padding: 0 20px
    margin-top: 40px
    @media screen and ( max-width: 1100px )
      font-size: 28px
    @media screen and ( max-width: 720px )
      font-size: 24px
    @media screen and ( max-width: 430px )
      font-size: 20px

  .breadcrumbs
    display: flex
    gap: 20px
    width: calc( 100% - 40px )
    height: 15px
    white-space: nowrap
    overflow-x: scroll
    overflow-y: hidden
    padding: 0 20px
    margin: 0
    font-size: 14px
    &::-webkit-scrollbar
      display: none
    li
      &:last-child::after
        display: none

      &::after
        content: "•"
        margin-left: 20px
        color: white

  .list-filter
    display: flex
    gap: 10px
    flex-wrap: wrap
    margin: 0
    padding: 0 20px
    li
      display: flex
      align-items: center
      gap: 10px
      background: rgba(0, 0, 0, 0.30)
      padding: 10px 10px
      border-radius: 5px
      text-transform: uppercase
      font-size: 14px
      cursor: pointer
      color: #fff

      &.active
        background: $green

      &:hover
        background: rgba(97, 97, 97, 0.459)

      img
        width: 20px

  .content-bets
    display: flex
    flex-direction: column
    gap: 10px
    padding: 20px 20px
    font-family: $main-font

    @media screen and ( max-width: 1440px )
      display: grid
      grid-template-columns: repeat(auto-fill, minmax(230px, 1fr))
    .bet
      display: grid
      grid-template-columns: 200px auto 1fr auto
      gap: 10px
      border-radius: 20px
      background: rgba(0, 0, 0, 0.30)
      padding: 10px 15px
      color: white
      @media screen and ( max-width: 1440px )
        grid-template-columns: 1fr

      .time-left
        display: flex
        align-items: center
        font-size: 14px
        width: 100%
        @media screen and ( max-width: 430px )
          font-size: 14px
        @media screen and ( max-width: 1440px )
          justify-content: center

        span
          color: #93BA61
          font-weight: bold
          @media screen and ( max-width: 430px )
            font-size: 14px
      .title
        display: flex
        gap: 10px
        align-items: center
        font-size: 14px

        p
          margin: 0

        img
          width: 20px

        img.cup
          width: 20px
          

        @media screen and ( max-width: 430px )
          font-size: 14px

        @media screen and ( max-width: 1440px )
          text-align: center
          justify-content: center
          gap: 10px
          img
            display: none

      .teams
        display: grid
        grid-template-columns: 1fr 1fr
        align-items: center
        justify-content: space-between
        gap: 10px

        @media screen and ( max-width: 1440px )
          grid-template-columns: 1fr 1fr
          align-items: flex-start

        .team-1,.team-2
          display: flex
          align-items: center
          justify-content: start
          flex-direction: row
          gap: 10px

          @media screen and ( max-width: 1440px )
            display: grid
            grid-template-columns: 1fr
            align-items: center
            justify-content: center

          .team-1-name, .team-2-name
            font-size: 14px
            @media screen and ( max-width: 1440px )
              justify-self: center
              text-align: center
            

          .team-1-name__alternate, .team-2-name__alternate
            @media screen and ( max-width: 1440px )
              justify-self: center
              text-align: center

          .team-1-logo, .team-2-logo
            grid-row: 1/span 2
            @media screen and ( max-width: 1440px )
              justify-self: center

        .text-vs
          text-align: center

        .team-1-logo, .team-2-logo
          height: 35px
          width: 35px

      a
        display: flex
        align-items: center

        justify-content: flex-end

      .main-btn
        border-radius: 10px
        background: $green
        cursor: pointer
        border: none
        padding: 10px 10px
        background: $green
        text-transform: uppercase
        color: white
        font-style: italic
        font-size: 14px
        transition: all .2s
        &:hover
          background: darken($green, 20%)

        @media screen and ( max-width: 1440px  )
          width: 100%

.seo-text
  color: #fff9
  font-family: $main-font
  font-size: 14px
  padding: 20px 20px 20px 20px
  border-top: solid 2px #fff9
  background: #272727
